import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { DjangoService } from '../django.service';

@Component({
  selector: 'app-top-nav',
  templateUrl: './top-nav.component.html',
  styleUrls: ['./top-nav.component.scss']
})
export class TopNavComponent implements OnInit {

  currentPath: String;
  userData: Object;


  constructor(
    private router: Router,
    private django: DjangoService,
  ) {
    this.django.currentUserDetails.subscribe(data => {
      this.userData = data
      this.currentPath = this.prettyUrl(this.router.url)
    })

    this.router.events.subscribe((urll) => {
      if (urll instanceof NavigationEnd) {
        this.currentPath = this.prettyUrl(urll['url'])
      }
      
    })
   }

  prettyUrl(url) {
    if (url === '/dashboard') {
      if (this.userData['teacher']) {
        url = '/Teacher Dashboard'
      }
    }

    url = "/" + url.replace("_", " ").split('/')[1]

    var newURL = url.slice(1, url.length); 
    return newURL.charAt(0).toUpperCase() + newURL.slice(1);
  }

  ngOnInit() {


  }

}
