import { Component, OnInit, Input, NgZone } from '@angular/core';
import { DjangoService } from '../django.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss'],
})
export class ProfileComponent implements OnInit {

  schools: Object;
  userState: boolean;
  userData: Object = {};
  model: Object;

  constructor(
    private django:DjangoService,
    private router:Router,
    private zone: NgZone) { }


  ngOnInit() {
    this.django.isUserLoggedIn.subscribe(data => {
      this.userState = data;
    })
    this.django.currentUserDetails.subscribe(data => {
      this.userData = data;
      this.model = data;
      
    })

  }


  public reRoute(route) {
    this.zone.run(() => { this.router.navigate([route]) })
  }


  onSubmit() {
    
    for (let cur in this.model) {
      sessionStorage.setItem(cur, this.model[cur])
       
    }

    if (this.userState === true) {
      this.django.updateProfile({username: sessionStorage.getItem('username'), desc: sessionStorage.getItem('bio'), school: sessionStorage.getItem("school_code"), teacher: sessionStorage.getItem('teacher')}).subscribe((data) =>
        this.django.currentUserDetails.next(
          {username: sessionStorage.getItem('username'),
           desc: sessionStorage.getItem('bio'),
           school: sessionStorage.getItem("school_code"),
           teacher: sessionStorage.getItem('teacher'),
           profile_image: this.userData['profile_image']
          })
      )
      this.reRoute('/dashboard')
    }
    else {
      this.reRoute('/permisson')
    }
  }

}
