import { Component, OnInit } from '@angular/core';
import { DjangoService } from '../django.service';

@Component({
  selector: 'app-enrolled',
  templateUrl: './enrolled.component.html',
  styleUrls: ['./enrolled.component.scss']
})
export class EnrolledComponent implements OnInit {

  constructor(
    private django: DjangoService,
  ) { }

  userData: Object;
  upcoming: Object;
  not_reg: Boolean = true;

  ngOnInit() {
    this.django.currentUserDetails.subscribe(
      userData => {
        this.userData = userData
        this.django.getRegClasses(this.userData['teacher'], 100).subscribe(data => {
          this.upcoming = data
          console.log(data)
          if (Object.keys(data).length < 1) {
          
            this.not_reg = true
          }
          else {

            this.not_reg = false
          }
        })
      }
    )
  }

}
